:root {
    --primary-color: #02153f;
    --secondary-color: #01aced;
}

* {
    box-sizing: border-box;
}

/* 
#032030
#022b42
#003554
#004d74
#006494
#006da4
*/

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');

* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Poppins', sans-serif;

    margin: auto;

}

/*  NAVBAR  */

header {
    padding: 1.7rem 0;
    display: flex;
    justify-content: space-around;
    background-color: transparent;
    align-content: center;
}

.logo a {
    font-size: 1.3rem;
    font-weight: 800;
    color: var(--primary-color);
    text-decoration: none;
}

nav {
    z-index: 1;
}

nav a {
    color: #fff;
    text-decoration: none;
    font-size: .97rem;
    font-weight: 600;
    padding: 0.4rem 1.3rem;

}

nav a:hover,
nav a.active {
    color: #26699b;
    transition: all 0.3s ease-in;
}

#click {
    display: none;
}

.menu {
    display: none;
}

/*NAVBAR*/
.overall-container {
    background: linear-gradient(90deg, rgb(255, 255, 255) 35%, #02153f 10%);
    height: 750px;
}


.main-section {
    margin: 2rem 8.5rem;
}

.main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 5rem auto;

}

.main h1 {
    font-size: 2.5rem;
    font-weight: 600;
    color: #fff;
}

.main p {
    margin-top: 1rem;
    font-size: .98rem;
    color: #fff;
}

.Information {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: auto;
}

.social {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-top: 1.7rem;
}

.social a {
    color: var(--secondary-color);
    font-size: 1rem;
    border: 1px solid #fff;
    border-radius: 50%;
    padding: 0.3rem;
    text-align: center;
    width: 27px;
    height: 25px;
    display: grid;
    place-items: center;

}

.social a:hover {
    color: #fff;
    background-color: var(--secondary-color);
    box-shadow: 0 0 20px var(--secondary-color);
    border-color: var(--secondary-color);
    transition: all .3s ease-in-out;

}

/* IMAGE*/

.images {
    width: 25em;
    height: 25em;
    border-radius: 50%;
    margin: auto;
}

.blue-bg-outer {
    width: 25em;
    height: 25em;
    border-radius: 50%;
    background: #02153f;
    display: grid;
    place-items: center;
}

.white-bg {
    width: 21.875em;
    height: 21.875em;
    border-radius: 50%;
    background: #fff;
    display: grid;
    place-items: center;
}

.blue-bg {
    width: 18.75em;
    height: 18.75em;
    border-radius: 50%;
    background: var(--primary-color);
}

.blue-bg img {

    width: 100%;
    border-radius: 50%;
}


/*BUTTON*/
.cv-btn {
    margin-top: 1.5rem;
    padding: 0.7em 2.5em;
    border-radius: 25px;
    background-color: #01aced;
    font-size: 1rem;
    font-weight: bold;
    color: white;
    border: 0;
}

.cv-btn:hover {
    background-color: #01aced;
    box-shadow: 0 0 10px #01aced;
    transition: all .3s ease-in-out;

}

.expereince-section {


    width: 1472px;


}

.grid-container {


    background-color: #cccfe0;
    width: 100%;
    display: grid;
    grid-template-columns: 34% 66%;
    box-sizing: border-box;
    text-align: left;

}

.first-element {

    /* border: 1px solid black; */
    border-top: 2px solid #cccfe0;
    ;

    padding-top: 20px;
    color: #003554;
    text-align: center;
    font-size: 25px;
    line-height: 27px;
}

.second-element {
    width: 100%;
    /* border: 1px solid black; */

    color: white
}


/* The actual timeline (the vertical ruler) */
.timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: var(--primary-color);
    /* background-color: #fff; */
    top: 0;
    bottom: 0;
    left: 14px;

}

/* Container around content */
.container {
    padding: 10px 40px;
    position: relative;
    background-color: #cccfe0;

}

/* The circles on the timeline */
.container::after {
    content: '';
    position: absolute;
    top: 25px;
    left: 0;
    width: 25px;
    height: 25px;

    background-color: white;
    border: 4px solid #FF9F55;

    border-radius: 50%;
    z-index: 1;
}

.content {
    margin-top: 10px;
    margin-bottom: 5px;
    margin-left: 10px;
    padding-left: 10px;
    background-color: white;
    border-radius: 7px;
    color: #003554;

}

.content ul {
    padding: 20px;
    list-style: disc;
}

.content ul li {
    line-height: 20px;
    font-size: 16px;
}

/*ANIMATION*/

.animated_text h1 {
    -webkit-text-stroke: 1px #fff;
    font-family: Arial, Helvetica, sans-serif;
    color: transparent;
    background-repeat: no-repeat;
    background-image: linear-gradient(#01aced, #01aced);
    -webkit-background-clip: text;
    background-clip: none;
    background-position: -500px 0;
    animation: backcolor 5s linear infinite;
}

@keyframes backcolor {

    100% {
        -webkit-text-stroke: 1px #01aced;
        background-position: 0 0;
    }
}

@media only screen and (max-width: 1024px) {
    html {
        font-size: 80%;
    }

    section {
        margin: 0;
    }


}

@media screen and (max-width: 768px) {
    html {
        font-size: 100%;
    }

    body {
        background: linear-gradient(180deg, rgb(255, 255, 255) 35%, #02153f 10%);
    }

    .menu {
        display: block;
    }

    .menu {
        font-size: 1.5rem;
        color: var(--primary-color);
    }

    header {
        padding: 0.7rem 1rem;
        justify-content: space-between;
    }

    nav {
        position: absolute;
        display: flex;
        flex-direction: column;
        text-align: center;
        background-color: #02153f;
        left: -100%;
        width: 100%;
        top: 3rem;
        transition: all 0.3s ease;
    }

    nav a {
        color: #fff;
    }

    #click:checked~nav {
        left: 0;
    }

    .Information {
        align-items: start;
    }

    .expereince-section {
        width: 768px;
    }

}

@media only screen and (max-width: 480px) {

    section {
        margin: 0 1rem;
    }

    .images {
        min-width: 15em;
    }

    .blue-bg-outer {
        width: 20em;
        height: 20em;
        margin: auto;

    }

    .white-bg {
        width: 16.875em;
        height: 16.875em;
    }

    .blue-bg {
        width: 12.75em;
        height: 12.75em;
    }

}

@media only screen and (max-width: 320px) {
    .images {
        height: 17em;
    }

    .blue-bg-outer {
        width: 16em;
        height: 16em;

    }

    .white-bg {
        width: 13em;
        height: 13em;
    }

    .blue-bg {
        width: 11em;
        height: 11em;
    }

    .blue-bg img {
        position: relative;

        width: 100%;
    }
}

.section-exp {
    /* background-color: #474e5d; */
    background-color: #cccfe0;
    font-family: Helvetica, sans-serif;
    border-radius: 10px;
    padding:15px 0;
}

.timeline-section {
    position: relative;
    max-width: 1472px;
    margin: 0 auto;
    padding: 15px 0;
}

/* The actual timelin-secftiom (the vertical ruler) */
.timeline-section::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: var(--primary-color);
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}

/* card-container around content */
.card-container {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
}

/* The circles on the timelin-secftiom */
.card-container::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: white;
    border: 4px solid #FF9F55;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}

/* Place the card-container to the left */
.left {
    left: 0;
}

/* Place the card-container to the right */
.right {
    left: 50%;
}

/* Add arrows to the left card-container (pointing right) */
.left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid white;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent white;
}

/* Add arrows to the right card-container (pointing left) */
.right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
}

/* Fix the circle for card-containers on the right side */
.right::after {
    left: -16px;
}

/* The actual content */
.card {
    padding: 20px 30px;
    background-color: white;
    position: relative;
    border-radius: 6px;
    
}
.card div{

    text-align: left;
}

/* Media queries - Responsive timelin-secftiom on screens less than 600px wide */
@media screen and (max-width: 600px) {

    /* Place the timelime to the left */
    .timelin-secftiom::after {
        left: 31px;
    }

    /* Full-width card-containers */
    .card-container {
        width: 100%;
        padding-left: 70px;
        padding-right: 25px;
    }

    /* Make sure that all arrows are pointing leftwards */
    .card-container::before {
        left: 60px;
        border: medium solid white;
        border-width: 10px 10px 10px 0;
        border-color: transparent white transparent transparent;
    }

    /* Make sure all circles are at the same spot */
    .left::after,
    .right::after {
        left: 15px;
    }

    /* Make all right card-containers behave like the left ones */
    .right {
        left: 0%;
    }
}